  position: absolute;
  left: 52%;
  text-align: center;
  top: 0;
}
.my-card {
  position: relative;
}
.my-card:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #ededed;
  color: #ededed;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
  left: 13px;
}
.margin-set {
  background: #fff;
  padding: 0 15px;
  height: 49px;
  display: flex;
  align-items: center;
}
.margin-set img {
  width: 20px;
}
.label {
  display: flex;
  margin-left: 15px;
  color: #191a1b;
  flex: 1;
  flex-direction: row;
}
.value {
  text-align: right;
  color: #797d82;
  padding-right: 14px;
}
.arrow {
  text-align: right;
  width: 6px;
  height: 10px;
}
.price-decimal {
  color: #797d82;
}
.price-fmt span {
  float: none !important;
}
.price-fmt i {
  font-size: 11px;
  font-style: normal;
}
</style>



